import InstaCtrl from './origins/Instagram'
import SpotiCtrl from './origins/Spotify'
import YoutubePlaylistCtrl from './origins/YoutubePlaylist'
import YoutubeVideoCtrl from './origins/YoutubeVideo'
import IframeCtrl, { IFRAME_MATCHER } from './origins/Iframe'
import { getContentCardForUnhandledOrigin } from './origins/UnhandledOrigin'
import { ContentCard } from '@/types/ContentCard'
import { OriginHandler } from '@/types/OriginHandler'

const URL_MATCHER = /^https?:\/\/[^\s/$.?#].[^\s]*$/

const getContentCardFromUserInput = async (userInput: string): Promise<ContentCard> => {
  if (!URL_MATCHER.test(userInput) && !IFRAME_MATCHER.test(userInput)) {
    throw new Error('Invalid iframe or URL! Make sure it starts with http(s)://...')
  }

  // Match the URL against handled origins
  const handledOrigins: OriginHandler[] = [InstaCtrl, SpotiCtrl, YoutubePlaylistCtrl, YoutubeVideoCtrl, IframeCtrl]
  let fullMatch = ''
  let p1 = ''
  let p2 = ''
  const contentHandler = handledOrigins.find(h => h.originMatchers.some(m => {
    [fullMatch, p1, p2] = userInput.match(m) || []
    return fullMatch
  }))

  if (!contentHandler || !p1 || p1.length < 1) {
    console.log('no origin matches', { contentHandler, fullMatch })
    return getContentCardForUnhandledOrigin(userInput)
  }

  const cardData: ContentCard = await contentHandler.getCardData(p1, p2)
  console.log({ cardData })
  return cardData
}

export {
  getContentCardFromUserInput,
  URL_MATCHER
}
