import { OriginHandler, GetAsyncCardData } from '@/types/OriginHandler'
import { ContentCard } from '@/types/ContentCard'
import { FetchUrlMeta } from '../FetchUrlMeta'

export const IFRAME_MATCHER = /^<iframe.+?src="(.+?)".*?>.*?<\/iframe>.+?$/

const originMatchers = [IFRAME_MATCHER]

const getCardData: GetAsyncCardData = async (iframeUrl: string): Promise<ContentCard> => {
  const { title, description } = await FetchUrlMeta(iframeUrl, '<EDIT WIDGET TITLE>', '<EDIT WIDGET DESCRIPTION>')

  return {
    type: 'iframe',
    url: iframeUrl,
    title,
    description,
    category: 'widget'
  }
}

const oh: OriginHandler = {
  originMatchers,
  getCardData
}

export default oh
