<template>
  <div>

    <Spinner v-if="loading"></Spinner>
    <EditProfile v-if="!loading" :profile-data="profileData">
    </EditProfile>

  </div>
</template>

<script language="ts">
import Spinner from '@/components/Spinner'
import { auth, profilesCollection } from '@/firebase.config'
import { newUserProfile } from '@/controllers/NewUserProfile'
import EditProfile from '@/components/EditProfile'

export default {
  metaInfo: {
    title: 'Edit Profile'
  },
  components: {
    EditProfile,
    Spinner
  },
  data () {
    return {
      profileData: {},
      loading: true
    }
  },
  mounted () {
    this.loading = true
    const { uid, displayName, photoURL } = auth.currentUser
    const profileRef = profilesCollection.doc(uid)

    profileRef.onSnapshot(async (profileDoc) => {
      if (!profileDoc || !profileDoc.exists) {
        const newProfile = {
          ...newUserProfile(),
          uid,
          displayName,
          photoURL
        }
        await profileRef.set(newProfile)
        this.profileData = newProfile
      } else {
        this.profileData = profileDoc.data()
      }

      this.loading = false
    })
  },
  beforeRouteUpdate () { // Change profile, force route re-entring
    document.location.reload()
  }
}
</script>
