import { OriginHandler, GetAsyncCardData } from '@/types/OriginHandler'
import { ContentCard } from '@/types/ContentCard'
import { FetchUrlMeta } from '../FetchUrlMeta'

const originMatchers = [/^(?:https?:\/\/)?(?:www\.)?(?:open\.)?spotify(?:\.com)?(?:\/|:)(playlist|track|album)(?:\/|:)(.+?)(?:\/.*)?$/]

const getCardData: GetAsyncCardData = async (spotiType: string, spotiId: string): Promise<ContentCard> => {
  const postUrl = `https://open.spotify.com/${spotiType}/${spotiId}`
  const url = `https://open.spotify.com/embed/${spotiType}/${spotiId}`

  const { title, description } = await FetchUrlMeta(postUrl)

  return {
    type: 'iframe',
    postUrl,
    url,
    title,
    description,
    category: 'spotify'
  }
}

const oh: OriginHandler = {
  originMatchers,
  getCardData
}

export default oh
