import { OriginHandler, GetAsyncCardData } from '@/types/OriginHandler'
import { ContentCard } from '@/types/ContentCard'
import { FetchUrlMeta } from '../FetchUrlMeta'

const originMatchers = [
  /^(?:https?:\/\/)?(?:www\.)?youtu\.be\/(?:.+?)\?list=(.+?)(?:\/.*)?$/,
  /^(?:https?:\/\/)?(?:www\.)?youtube\.com\/playlist\?list=(.+?)(?:\/.*)?$/,
  /^(?:https?:\/\/)?(?:www\.)?youtube\.com\/watch\?v=.+?&list=(.+?)(?:\/.*)?$/,
  /^(?:https?:\/\/)?(?:www\.)?youtube\.com\/watch\?list=(.+?)(?:&v=.+?)?(?:\/.*)?$/
]

const getCardData: GetAsyncCardData = async (playListId: string): Promise<ContentCard> => {
  const postUrl = `https://www.youtube.com/playlist?list=${playListId}`
  const url = `https://www.youtube.com/embed/videoseries?list=${playListId}`

  const { title, description } = await FetchUrlMeta(url)

  return {
    type: 'iframe',
    postUrl,
    url,
    title,
    description,
    category: 'youtube'
  }
}

const oh: OriginHandler = {
  originMatchers,
  getCardData
}

export default oh
