<template>
  <div v-bind:style="`background-color: ${updateColor || profileData.bgColor}; color: ${invertHex}`" class="card profile m-3 m-md-4 p-3 p-lg-4">

    <ProfileHeader :edit-mode="true" :uid="currentUserId"></ProfileHeader>

    <div class="d-flex justify-content-center">
      <font-awesome-icon v-if="updateColor !== profileData.bgColor" @click="updateColor = profileData.bgColor" class="fa-2x p-1 is-clickable rounded-circle" :icon="['fa', 'undo-alt']"/>
      <color-picker v-if="profileData.bgColor" :width=120 :height=120 v-model="updateColor" :startColor="profileData.bgColor">
      </color-picker>
      <font-awesome-icon v-if="updateColor !== profileData.bgColor" @click="updateProfileField('bgColor', updateColor)" class="fa-2x p-1 is-clickable rounded-circle" :icon="['fa', 'check']"/>
    </div>

    <!-- Cover Image -->
    <span class="text-left m-0 p-1">
      <font-awesome-icon @click="globalConfirm('⚠️ Load random demo Cover photo? You´ll lose the current URL...') ? updateProfileField('coverImg', randomCoverUrl(), 'url') : null" class="is-clickable fa-lg mx-2" :icon="['fa', 'random']"/>
      <font-awesome-icon @click="toggles.coverImg = !toggles.coverImg" class="is-clickable fa-lg mx-2" :icon="['fa', 'pencil-alt']"/>
      <span v-if="toggles.coverImg" id="coverImg" :contenteditable="true" v-on:blur="updateProfileField('coverImg', domFieldValue('coverImg'), 'url'); toggles.coverImg=false">{{profileData.coverImg}}</span>
    </span>
    <img class="card-img-top cover-img" v-bind:src="profileData.coverImg"/>
    <!-- /Cover Image -->

    <div class="card-body text-center">

      <!-- Profile Picture -->
      <img class="avatar rounded-circle" v-bind:style="{ 'border-color': invertHex }" :src="profileData.photoURL"/>
      <div>
        <font-awesome-icon @click="globalConfirm('⚠️ Load random demo Profile photo? You´ll lose the current URL...') ? updateProfileField('photoURL', randomCoverUrl(), 'url') : null" class="is-clickable fa-lg mx-2" :icon="['fa', 'random']"/>
        <font-awesome-icon @click="toggles.photoURL = !toggles.photoURL" class="is-clickable fa-lg mx-2" :icon="['fa', 'pencil-alt']"/>
        <div v-if="toggles.photoURL" id="photoURL" :contenteditable="true" v-on:blur="updateProfileField('photoURL', domFieldValue('photoURL'), 'url'); toggles.photoURL=false">{{profileData.photoURL}}</div>
      </div>
      <!-- /Profile Picture -->

      <hr v-bind:style="`background-color: ${invertHex}`" class="mx-4 text-muted" />

      <!-- Display Name -->
      <div class="is-clickable hover-shadow">
        <h4 class="card-title">
          <font-awesome-icon class="mx-1" :icon="['fa', 'pencil-alt']"/>
          <span id="displayName" :contenteditable="true" v-on:blur="updateProfileField('displayName', domFieldValue('displayName'))">
            {{profileData.displayName}}
          </span>
        </h4>
      </div>
      <!-- /Display Name -->

      <!-- Short Bio -->
      <div class="is-clickable hover-shadow">
        <h6 class="card-subtitle mb-2 text-muted">
          <font-awesome-icon class="mx-1" :icon="['fa', 'pencil-alt']"/>
          <span id="shortBio" :contenteditable="true" v-on:blur="updateProfileField('shortBio', domFieldValue('shortBio'))">
            {{profileData.shortBio}}
          </span>
        </h6>
      </div>
      <!-- /Short Bio -->

      <!-- Social Links -->
      <div class="d-flex justify-content-center mt-4">
        <font-awesome-icon @click="editSocial = key" v-bind:class="{ 'text-muted': socialLink.link.startsWith('https://...'), 'border-bottom': editSocial === key }" v-for="(socialLink, key) in profileData.socialLinks" :key="key" class="is-clickable fa-2x mx-1 p-1" :icon="socialLink.icon"/>
      </div>
      <div v-if="editSocial" class="text-center mt-3 mx-1 is-clickable">
        <font-awesome-icon class="fa-lg mx-1 p-1" :icon="['fa', 'pencil-alt']"/>
        <span id="socialLink" :contenteditable="true" v-on:blur="updateSocialLinks()">{{profileData.socialLinks[editSocial].link}}</span>
      </div>
      <!-- /Social Links -->

    </div>

    <hr v-bind:style="`background-color: ${invertHex}`" class="mx-4" />

    <!-- Long Bio -->
    <h4 class="font-weight-bold text-muted">Bio</h4>
    <div class="is-clickable hover-shadow">
      <h6 v-if="!centerBio">
        <font-awesome-icon class="fa-lg mx-1 p-1" :icon="['fa', 'pencil-alt']"/>
        <span id="longBio" :contenteditable="true" v-on:blur="updateProfileField('longBio', domFieldValue('longBio'))">
          {{profileData.longBio}}
        </span>
      </h6>
      <h5 v-if="centerBio" class="text-center">
        <font-awesome-icon class="fa-lg mx-1 p-1" :icon="['fa', 'pencil-alt']"/>
        <span id="longBio" :contenteditable="true" v-on:blur="updateProfileField('longBio', domFieldValue('longBio'))">
          {{profileData.longBio}}
        </span>
      </h5>
    </div>
    <!-- /Long Bio -->

    <hr v-bind:style="`background-color: ${invertHex}`" class="mx-4" />

    <div class="row">
      <div class="col-6 text-center">
        <font-awesome-icon class="fa-lg mx-4" :icon="['fa', 'mobile-alt']"/>
        <font-awesome-icon @click="()=>{ if(profileData.numMobileColumns > 1) { updateProfileField('numMobileColumns', profileData.numMobileColumns-1, 'number', false) } }" class="is-clickable fa-lg" :icon="['fa', 'minus-square']"/>
        <span id="numMobileColumns" class="mx-2">{{profileData.numMobileColumns}}</span>
        <font-awesome-icon @click="()=>{ if(profileData.numMobileColumns < 2) { updateProfileField('numMobileColumns', profileData.numMobileColumns+1, 'number', false) } }" class="is-clickable fa-lg" :icon="['fa', 'plus-square']"/>
      </div>
      <div class="col-6 text-center">
        <font-awesome-icon class="fa-lg mx-3" :icon="['fa', 'desktop']"/>
        <font-awesome-icon @click="()=>{ if(profileData.numDesktopColumns > 1) { updateProfileField('numDesktopColumns', profileData.numDesktopColumns-1, 'number', false) } }" class="is-clickable fa-lg" :icon="['fa', 'minus-square']"/>
        <span id="numDesktopColumns" class="mx-2">{{profileData.numDesktopColumns}}</span>
        <font-awesome-icon @click="()=>{ if(profileData.numDesktopColumns < 5) { updateProfileField('numDesktopColumns', profileData.numDesktopColumns+1, 'number', false) } }" class="is-clickable fa-lg" :icon="['fa', 'plus-square']"/>
      </div>
    </div>

    <hr v-bind:style="`background-color: ${invertHex}`" class="mx-4" />

    <!-- Add content -->
    <h4 class="font-weight-bold text-muted">Links &amp; &lt;iframe> widgets</h4>
    <div v-if="!scrappingURL" class="text-center my-3 px-0 mx-4">
      <div class="mx-auto mb-3"><a class="text-reset" href="https://blog.instab.io/#/guide/instab.io-101" target="_blank">🤓 Read the Guide of Use 📚</a></div>
      <input v-bind:placeholder="`Share your magic here... ${getEmojis(randomInRange(1, 3))} `" type="text" class="pl-3 py-2 align-middle content-input w-100 mb-3" v-model="addContentUserInput" />
      <font-awesome-icon @click="addContent" v-bind:class="{'text-reset border rounded is-clickable': validUserInput }" class="p-1 fa-4x text-muted" :icon="['fa', 'cloud-upload-alt']"/>
    </div>
    <div v-if="scrappingURL" class="text-center my-3 px-0 mx-4">
        <h5>⏳ Fetching link data, should be quick... 🤞</h5>
        <div class="spinner-border" role="status"></div>
    </div>
    <!-- Add content -->

    <!-- Content Cards -->
    <div class="row mt-3 text-secondary" v-bind:class="`row-cols-${profileData.numMobileColumns} row-cols-md-${profileData.numDesktopColumns}`">
      <EditContentCard v-for="(contentCard, contentCardId) in sortedCards"
        :handleDragStart="handleDragStart"
        :handleDragOver="handleDragOver"
        :handleDrop="handleDrop"
        :handleDragEnd="handleDragEnd"
        :id="contentCardId"
        v-bind:style="{ border: getCardBorder(contentCardId), 'background-color': getCardBgColor(contentCardId) }"
        :allCategories="allCategories"
        :key="contentCardId"
        :contentCard="contentCard"
        :content-card-id="contentCardId"
        :delete-content-card="deleteContentCard"
        :editContentCard="editContentCard"
      >
      </EditContentCard>
    </div>
    <!-- /Content Cards -->

    <div v-if="totalRows > perPage" class="my-5">
      <b-pagination
        class="pagination"
        align="center"
        v-model="currentPage"
        :total-rows="totalRows"
        :per-page="perPage"
      ></b-pagination>
    </div>

  </div>
</template>

<script language="ts">
import ProfileHeader from '@/components/ProfileHeader'
import firebase from 'firebase/app'
import { auth, profilesCollection } from '@/firebase.config'
import { getContentCardFromUserInput, URL_MATCHER } from '@/controllers/UserInputHandler'
import { IFRAME_MATCHER } from '@/controllers/origins/Iframe'
import EditContentCard from './EditContentCard'
import ColorPicker from 'vue-color-picker-wheel'
import { getEmojis, randomInRange, getRandomFromArr } from '@/utils/utils'
import bgImgs from '@/assets/bg_imgs.json'
import profileImgs from '@/assets/profile_imgs.json'

export default {
  components: {
    ProfileHeader,
    EditContentCard,
    ColorPicker
  },
  props: {
    profileData: {}
  },
  data () {
    return {
      currentUserId: auth?.currentUser?.uid,
      editSocial: null,
      addContentUserInput: '',
      toggles: {
        coverImg: false,
        photoURL: false
      },
      updateColor: '',
      URL_MATCHER,
      IFRAME_MATCHER,
      dragSrcId: '',
      lastTouchedId: '',
      perPage: 10,
      currentPage: 1,
      scrappingURL: false
    }
  },
  computed: {
    validUserInput () { return URL_MATCHER.test(this.addContentUserInput) || IFRAME_MATCHER.test(this.addContentUserInput) },
    centerBio () { return this.profileData.longBio?.length <= 140 },
    compColor () { return this.updateColor || this.profileData.bgColor },
    populatedSocialLinks () {
      if (!this.profileData?.socialLinks) return []
      return Object.keys(this.profileData.socialLinks).reduce((p, c) => {
        if (!this.profileData.socialLinks[c].link.startsWith('https://...')) p[c] = this.profileData.socialLinks[c]
        return p
      }, {})
    },
    sortedKeys () { return this.profileData?.contentCards ? Object.keys(this.profileData.contentCards).reverse() : [] },
    totalRows () { return this.sortedKeys.length },
    sortedCards () {
      const skip = this.currentPage > 1 ? (this.currentPage - 1) * this.perPage : 0
      const limit = skip + this.perPage
      return this.sortedKeys.slice(skip, limit).reduce((p, c) => {
        p[c] = this.profileData.contentCards[c]
        return p
      }, {})
    },
    invertHex () {
      const hex = this.compColor?.slice(1)
      return hex ? `#${(Number(`0x1${hex}`) ^ 0xFFFFFF).toString(16).substr(1).toUpperCase()}` : ''
    },
    allCategories () {
      return this.sortedKeys
        .map(ccKey => this.profileData.contentCards[ccKey].category)
        .filter(cat => Boolean(cat))
    }
  },
  methods: {
    handleDragStart (id) { this.dragSrcId = id },
    handleDragOver (id) { this.lastTouchedId = id },
    handleDrop () {
      if (this.dragSrcId !== this.lastTouchedId) {
        this.swapContentCards(this.dragSrcId, this.lastTouchedId)
      }
    },
    handleDragEnd () {
      this.dragSrcId = ''
      this.lastTouchedId = ''
    },
    getEmojis,
    randomInRange,
    globalConfirm (str) { return confirm(str) },
    randomCoverUrl () { return getRandomFromArr(bgImgs) },
    randomProfileUrl () { return getRandomFromArr(profileImgs) },
    makeToast (msg, title = 'Info', variant = 'secondary', autoHideDelay = 2000, toaster = 'b-toaster-top-right') {
      this.$bvToast.toast(msg, {
        title,
        variant,
        toaster,
        autoHideDelay
      })
    },
    domFieldValue (field) { return document.getElementById(field)?.textContent },
    getCardBorder (contentCardId) {
      if (!this.dragSrcId && !this.lastTouchedId) return ''
      if (this.dragSrcId === contentCardId) return `1px dashed ${this.compColor}`
      if (this.lastTouchedId === contentCardId) return `1px dashed ${this.compColor}`
      return ''
    },
    getCardBgColor (contentCardId) {
      if (!this.dragSrcId && !this.lastTouchedId) return ''
      if (this.dragSrcId === contentCardId) return this.invertHex
      if (this.lastTouchedId === contentCardId) return this.invertHex
      return ''
    },
    async addContent () {
      const { uid } = auth?.currentUser
      if (!uid) return
      this.scrappingURL = true
      try {
        const newContentCard = await getContentCardFromUserInput(this.addContentUserInput.trim())
        this.addContentUserInput = ''
        const profileRef = profilesCollection.doc(uid)
        await profileRef.update(`contentCards.${new Date().getTime()}`, newContentCard)
        this.makeToast('New link posted! 👍')
      } catch ({ code, message }) {
        console.error('addContent Error', { code, message })
        this.makeToast(message, 'Oooooops! ⚠️', 'warning', 7000)
      }
      this.scrappingURL = false
    },
    deleteContentCard (contentCardId) {
      const { uid } = auth?.currentUser
      if (!uid) return
      if (confirm('⚠️ No regrets? ⚠️')) {
        const profileRef = profilesCollection.doc(uid)
        profileRef.update({
          [`contentCards.${contentCardId}`]: firebase.firestore.FieldValue.delete()
        })
        this.makeToast('Yippee ki yay! 🗑️')
      }
    },
    async updateProfileField (pathAndDomId, newVal, fieldType = 'string', notify = true) {
      const { uid } = auth?.currentUser
      if (!uid) return

      if (fieldType === 'url' && !this.URL_MATCHER.test(newVal)) {
        this.makeToast('You better use the good old copy-paste...', 'Invalid URL! ⚠️', 'warning')
        return
      }

      const [path, domId] = typeof pathAndDomId === 'string' ? [pathAndDomId, pathAndDomId] : pathAndDomId

      const keysFromPath = path.split('.')
      const nestingLevel = keysFromPath.length
      const [key1, key2, key3] = keysFromPath

      let oldVal = null
      switch (nestingLevel) {
        case 1:
          oldVal = this.profileData[key1]
          break
        case 2:
          oldVal = this.profileData[key1][key2]
          break
        case 3:
          oldVal = this.profileData[key1][key2][key3]
          break
        default: return
      }

      if (fieldType === 'url' || fieldType === 'string') {
        newVal = newVal.trim()
        oldVal = oldVal.trim()
        if (!newVal || newVal.length < 1) {
          console.log('no value')
          this.makeToast('Empty field! ⚠️', 'Invalid value', 'warning')
          const domEl = document.getElementById(domId)
          if (domEl) domEl.innerHTML = oldVal
          return
        }
      }

      if (JSON.stringify(newVal) === JSON.stringify(oldVal)) {
        console.log('no change')
        return
      }
      console.log({ oldVal, newVal })
      const profileRef = profilesCollection.doc(uid)
      await profileRef.update(path, newVal)
      if (notify) this.makeToast('Profile updated! 👍')
    },
    updateSocialLinks () {
      const { editSocial } = this
      this.editSocial = null
      const link = this.domFieldValue('socialLink')
      this.updateProfileField([`socialLinks.${editSocial}.link`, 'socialLink'], link, 'url')
    },
    editContentCard (contentCardId, key, domIdOrValue) {
      if (key === 'title' || key === 'description') {
        this.updateProfileField([`contentCards.${contentCardId}.${key}`, domIdOrValue], this.domFieldValue(domIdOrValue))
      } else {
        this.updateProfileField(`contentCards.${contentCardId}.${key}`, domIdOrValue, 'Array')
      }
    },
    swapContentCards (id1, id2) {
      this.updateProfileField(`contentCards.${id1}`, this.profileData.contentCards[id2], 'Object', false)
      this.updateProfileField(`contentCards.${id2}`, this.profileData.contentCards[id1], 'Object', true)
    }
  }
}
</script>

<style lang="css">
/* Custom CSS here */

[draggable=true] {
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-drag: element;
  -webkit-user-drag: element;
}

.is-draggable {
  cursor: move;
}

.profile .avatar {
  height: 150px;
  width: 150px;
  object-fit: cover;
  margin-top: -6rem;
  margin-bottom: 1rem;
}

.profile .cover-img {
  height: 250px;
  object-fit: cover;
}

.profile .content-input {
  border: none;
  border-radius: 20px;
}

.is-clickable:hover {
  cursor: pointer;
  transform: scale(1.05,1.05);
}

.hover-shadow:hover {
  box-shadow: 0px 20px 40px rgba(0,0,0,0.4);
}
</style>
