import axios from 'axios'
import cheerio from 'cheerio'
import { SiteMeta } from '@/types/OriginHandler'
import { getEmojis, randomInRange } from '@/utils/utils'
import { URL_MATCHER } from './UserInputHandler'

const solveImgUrl = (inputUrl: string, imgPath: string): string => {
  if (URL_MATCHER.test(imgPath)) return imgPath
  const { hash, host, hostname, href, origin, password, pathname, port, protocol, search } = new URL(`${inputUrl} ${imgPath.startsWith('/') ? imgPath : '/'.concat(imgPath)}`)
  console.log({ hash, host, hostname, href, origin, password, pathname, port, protocol, search })
  return `${origin}${imgPath}`
}

export const FetchUrlMeta = async (postUrl: string, defaultTitle = '<CLICK TO EDIT TITLE>', defaultDescription = '<CLICK TO EDIT DESCRIPTION>'): Promise<SiteMeta> => {
  const images: string[] = []
  let meta: SiteMeta = {
    headers: [],
    data: '',
    title: '',
    description: '',
    images: []
  }

  try {
    meta = {
      ...meta, ...await axios.get(`https://cors-anywhere.herokuapp.com/${postUrl}`, { maxRedirects: 0 })
    }
  } catch ({ code, message }) {
    meta = {
      ...meta, ...await axios.get(postUrl, { maxRedirects: 0 })
    }
  }

  try {
    const $ = cheerio.load(meta.data as string)

    const ogTitle = $('meta[name="og:title"]')?.prop('content')?.trim().slice(0, 50)
    const rawTitle = $('title')?.text()?.trim().slice(0, 50)
    const title = `${ogTitle || rawTitle || defaultTitle} ${getEmojis(randomInRange(1, 2))}`

    const ogDescription = $('meta[name="og:description"]')?.prop('content')?.trim().slice(0, 140)
    const rawDescription = $('meta[name="description"]')?.prop('content')?.trim().slice(0, 140)
    const description = `${ogDescription || rawDescription || defaultDescription} ${getEmojis(randomInRange(1, 3))}`

    const ogImage = $('meta[property="og:image"]')?.prop('content')
    if (ogImage) {
      images.push(solveImgUrl(postUrl, ogImage))
    } else {
      $('img').map((i, ele) => {
        const src = $(ele).prop('src')
        if (src) {
          images.push(solveImgUrl(postUrl, src))
        }
      })
    }

    // sort images by URL length
    images.sort((a, b) => a.length - b.length)

    console.log({ ...meta, title, description, images })
    return { ...meta, title, description, images }
  } catch ({ code, message }) {
    console.error({ code, message })
    return {
      ...meta,
      title: `${defaultTitle} ${getEmojis(randomInRange(1, 2))}`,
      description: `${defaultDescription} ${getEmojis(randomInRange(1, 3))}`,
      images
    }
  }
}
