<template>
  <div class="col mb-4 is-draggable"
    draggable="true"
    @dragstart="handleDragStart(contentCardId)"
    @dragover.prevent="handleDragOver(contentCardId)"
    @drop.stop="handleDrop"
    @dragend="handleDragEnd"
  >
    <div class="card h-100">
      <vue-bootstrap-typeahead v-if="contentCard.type == 'remote_link'"
        placeholder="🔎 Find another icon..."
        :data="allIcons"
        :serializer="s => s[1].replace(/-/g,' ')"
        ref="typeahead_icon"
        @hit="iconSelected"
      />
      <div class="card-img-top embed-responsive embed-responsive-4by3">

        <!-- external_link -->
        <a v-if="contentCard.type == 'remote_link'" target="_blank" :href="contentCard.postUrl" class="text-reset embed-responsive-item">
          <font-awesome-icon :id="`cc-icon-${contentCardId}`" class="embed-responsive-item pt-3 pb-5" :icon="contentCard.icon"/>
        </a>
        <!-- /external_link -->

        <!-- ig_image remote_image -->
        <a v-if="['ig_image', 'remote_image'].includes(contentCard.type)" target="_blank" :href="contentCard.postUrl" class="text-reset">
          <img style="object-fit: cover;" class="embed-responsive-item" :src="contentCard.url"/>
        </a>
        <!-- /ig_image remote_image -->

        <!-- ig_video -->
        <video v-if="contentCard.type === 'ig_video'" class="embed-responsive-item" controls>
          <source :src="contentCard.url">
        </video>
        <!-- /ig_video -->

        <!-- iframe -->
        <iframe v-if="contentCard.type === 'iframe'" class="embed-responsive-item" :src="contentCard.url"
          allowtransparency="true"
          frameborder="0"
          allow="accelerometer; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
        <!-- /iframe -->

      </div>
      <div class="card-body">

        <!-- title -->
        <div>
          <p class="card-title hover-shadow is-clickable">
            <font-awesome-icon class="mx-1" :icon="['fa', 'pencil-alt']"/>
            <span :id="`cc-title-${contentCardId}`" :contenteditable="true" v-on:blur="editContentCard(contentCardId, 'title', `cc-title-${contentCardId}`)">
              {{contentCard.title}}
            </span>
          </p>
        </div>
        <!-- /title -->

        <!-- description -->
        <div>
          <p class="card-text hover-shadow is-clickable">
            <font-awesome-icon class="mx-1" :icon="['fa', 'pencil-alt']"/>
            <span :id="`cc-body-${contentCardId}`" :contenteditable="true" v-on:blur="editContentCard(contentCardId, 'description', `cc-body-${contentCardId}`)">
              {{contentCard.description}}
            </span>
          </p>
        </div>
        <!-- /description -->

      </div>

      <vue-bootstrap-typeahead
        :placeholder="`${contentCard.category ? '#'+contentCard.category + ' 🏷️ (click to edit)' : '🏷️ Choose a category!'}`"
        :data="Array.from(new Set(currentCat ? [currentCat, ...allCategories] : allCategories))"
        ref="typeahead_category"
        @hit="categorySelected"
        v-model="currentCat"
      />

      <div class="d-inline text-right py-2 pr-2">
        <font-awesome-icon v-if="contentCard.postUrl && contentCard.postUrl.trim().length" class="is-clickable mx-2" @click="showSocialShare(contentCard.postUrl, contentCard.title)" :icon="['fa', 'share-alt']"/>
        <font-awesome-icon class="is-clickable mx-2" @click="deleteContentCard(contentCardId)" :icon="['fa', 'trash']"/>
      </div>
    </div>
  </div>

</template>

<script language="ts">
import EventBus from '@/utils/EventBus'
import { allIcons } from '@/utils/all_icons'

export default {
  props: {
    allCategories: Array,
    contentCardId: String,
    contentCard: Object,
    deleteContentCard: Function,
    editContentCard: Function,
    handleDragStart: Function,
    handleDragOver: Function,
    handleDrop: Function,
    handleDragEnd: Function
  },
  data () {
    return {
      allIcons,
      currentCat: ''
    }
  },
  methods: {
    showSocialShare (url, socialShareText) {
      EventBus.$emit('SHOW_SOCIAL', { url, socialShareText })
    },
    iconSelected (icon) {
      this.$refs.typeahead_icon.inputValue = ''
      this.editContentCard(this.contentCardId, 'icon', icon)
    },
    categorySelected (category) {
      if (!this.$refs.typeahead_category.inputValue || this.$refs.typeahead_category.inputValue.length < 1) return
      this.$refs.typeahead_category.inputValue = ''
      this.editContentCard(this.contentCardId, 'category', category.replace(/#/g, ''))
    }
  }
}
</script>
