import * as fa from '@fortawesome/free-solid-svg-icons'
import * as fab from '@fortawesome/free-brands-svg-icons'
import { toKebabCase } from './utils'

const allFa = Object.keys(fa).filter(k => k.length > 3 && k.startsWith('fa'))
const allFab = Object.keys(fab).filter(k => k.length > 3 && k.startsWith('fa'))

export const allIcons = [
  ...allFa.map(fa => (['fa', toKebabCase(fa).slice(3)])),
  ...allFab.map(fab => (['fab', toKebabCase(fab).slice(3)]))
]
