import cheerio from 'cheerio'
import { OriginHandler, GetAsyncCardData } from '@/types/OriginHandler'
import { ContentCard, ContentCardType } from '@/types/ContentCard'
import { FetchUrlMeta } from '../FetchUrlMeta'

const originMatchers = [
  /^(?:https?:\/\/)?(?:www\.)?instagram\.com\/p\/(.+?)(?:\/.*)?$/
]

const getCardData: GetAsyncCardData = async (postId: string): Promise<ContentCard> => {
  const postUrl = `https://www.instagram.com/p/${postId}/?utm_source=ig_web_button_share_sheet`

  const { title, description, data } = await FetchUrlMeta(postUrl)
  const $ = cheerio.load(data as string)
  const medium = $('meta[name="medium"]').prop('content')

  if (!['video', 'image'].some(t => t === medium)) {
    console.log('Unknown insta medium', { medium })
    throw new Error('An unknown insta ocurred')
  }

  const url = $(`meta[property="og:${medium}"]`).prop('content')
  const type: ContentCardType = medium.includes('video') ? 'ig_video' : 'ig_image'

  return {
    type,
    postUrl,
    url,
    title,
    description,
    category: 'instagram'
  }
}

const oh: OriginHandler = {
  originMatchers,
  getCardData
}

export default oh
