import { getEmojis, randomInRange } from '@/utils/utils'
import { ContentCard } from '@/types/ContentCard'
import { GetAsyncCardData } from '@/types/OriginHandler'
import { FetchUrlMeta } from '../FetchUrlMeta'

const isImage = (contentType: string) => {
  return contentType.startsWith('image/')
}

const getContentCardForUnhandledOrigin: GetAsyncCardData = async (url: string): Promise<ContentCard> => {
  try {
    // Might be an image then, axios da bitch's content-type...
    const { headers: { 'content-type': contentType }, title, description, images } = await FetchUrlMeta(url)

    if (isImage(contentType)) {
      return {
        type: 'remote_image',
        url,
        title: '<CLICK TO EDIT IMAGE TITLE>',
        description: '<CLICK TO EDIT IMAGE DESCRIPTION>',
        postUrl: url,
        category: 'image'
      }
    }

    const [imageUrl] = images
    return imageUrl ? {
      type: 'remote_image',
      url: imageUrl,
      title,
      description,
      postUrl: url,
      category: 'external link'
    } : {
      type: 'remote_link',
      icon: ['fa', 'link'],
      url,
      title,
      description,
      postUrl: url,
      category: 'external link'
    }
  } catch ({ code, message }) {
    console.error({ code, message })
    return {
      type: 'remote_link',
      icon: ['fa', 'link'],
      url,
      title: `<CLICK TO EDIT LINK TITLE> ${getEmojis(randomInRange(1, 2))}`,
      description: `<CLICK TO EDIT LINK DESCRIPTION> ${getEmojis(randomInRange(1, 3))}`,
      postUrl: url,
      category: 'external link'
    }
  }
}

export {
  getContentCardForUnhandledOrigin
}
